.header {
  .logo {
    max-width: 200px;
    width: 100%;

    @include below($xs-max) {
      max-width: 150px;
    }
  }

  &-nav {
    max-width: 475px;
    width: 100%;

    &__link {
      position: relative;

      &::before {
        margin-bottom: -12px;
        content: '';
        bottom: 0;
        left: 0;
        position: absolute;
        width: 0;
        height: 3px;
        background-color: $white;
        cursor: pointer;
        transition: 0.2s;
      }

      &:hover:before {
        width: 100%;
      }

      &:hover {
        cursor: pointer;
      }
    }
  }

  .lang-switch {
    max-height: 40px;
    max-width: 90px;
    background: $white;
    border-radius: 5px;

    @include above($lg-min) {
      margin-right: 50px;
    }

    @include below($md-max) {
      margin-left: auto;
    }

    &__item {
      padding: 5px;
    }

    &_active {
      color: #9F9F9F !important;
    }
  }
}

.a-header::before {
  margin-bottom: -12px;
  content: '';
  bottom: 0;
  left: 0;
  position: absolute;
  width: 0%;
  height: 3px;
  background-color: #8EA253;
  cursor: pointer;
  transition: 0.2s;
}

.a-header:hover:before {
  width: 100%;
}

.a-header:hover {
  cursor: pointer;
}