.btn {
  padding: 15px 20px;
  background: $white;
  border-radius: 5px;
  color: $black;
  transition: 1s;

  &:hover {
    background: transparent;
    color: $white !important;
  }
}

.btn_card  {
  padding: 15px 20px;
  background: $white;
  color: $black;
  border-radius: 10px;
  transition: 1s;

  &:hover {
    background: darken($white, 20%);
  }
}

.btn-submit {
  padding: 15px 50px;
  background: $green;
  border: none;
  border-radius: 5px;
  transition: 1s;
  cursor: pointer;

  &:hover {
    background: darken($green, 15%);
  }
}