$white: #FFFFFF;
$black: #1B1C1E;
$red: #EF1A1A;
$blue: #1A64B1;
$green: #27A825;
$light-red: #E87573;
$shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);

.c-white {
  color: $white !important;
}

.c-black {
  color: $black !important;
}

.pn-shadow {
  box-shadow: $shadow;
}

.c-blue {
color: $blue !important;
}

.c-green {
  color: $green !important;
}

.c-red {
  color: $light-red !important;
}