.text {
  font-family: 'Montserrat', sans-serif;
  font-size: 22px;
  font-weight: 500;
  line-height: 33px;
  color: $white;

  &_sm {
    font-size: 14px;
    font-weight: 600;
    line-height: 17px;
  }

  &_md {
    font-size: 18px;
    font-weight: 600;
    line-height: 22px;
  }

  &_lg {
    font-size: 24px;
    line-height: 29px;
  }

  &_xl {
    font-size: 32px;
    line-height: 40px;
  }
}

.title {
  font-family: 'Quicksand', sans-serif;
  font-size: 24px;
  font-weight: 700;
  line-height: 30px;
  color: $white;

  &_sm {
    font-size: 34px;
    line-height: 37px;

    @include below($xs-max) {
      font-size: 30px;
      line-height: 32px;
    }
  }

  &_md {
    font-size: 36px;
    font-weight: 600;
    line-height: 44px;

  }

  &_lg {
    font-size: 46px;
    line-height: 51px;
  }
}
