body {
  background: #141313;
}

.hero {
  @include below($md-max) {
    margin-bottom: 40px;
  }
}

.more {
  &-card {
    max-width: 356px;
    padding: 20px 25px;
    border-radius: 10px;
    background: $white;

    &:hover {
      animation: bounce 1s;
    }

    &__img {
      max-width: 150px;
    }

    &_white {
      padding: 40px 15px;
      background: $black;
    }
  }
}

.about {
  &-wrap {
    padding: 50px;
    margin-bottom: -210px;
    background-color: rgba(0, 0, 0, 0.6);

    @include below($md-max) {
      margin-bottom: 0;
    }

    @include below($sm-max) {
      padding: 15px;
    }
  }

  &__img {
    z-index: -1;
  }
}

.form {
  &-wrap {
    padding: 50px 75px;
    background: #292B2D;
    border-radius: 20px;

    @include below($xs-max) {
      padding: 20px;
    }

    .wrapper-input {
      max-width: 375px;
      width: 100%;

      @include below($lg-max) {
        max-width: initial;
      }

      .input {
        width: 100%;
        height: 55px;
        padding: 15px 20px;
        border: none;
        border-radius: 10px;
        margin-bottom: 20px;
      }
    }
  }
}

.card {

  @keyframes bounce {
    0%, 20%, 60%, 100% {
      transform: translateY(0);
    }

    40% {
      transform: translateY(-20px);
    }

    80% {
      transform: translateY(-10px);
    }
  }

  &-slide:hover {
    animation: bounce 1s;
  }

  &-top {
    padding: 10px;
    width: 100%;
    position: relative;
    border-radius: 10px 10px 0 0;
    background: $white;

    &__logo {
      position: absolute;
      max-width: 130px;
    }

    &__type {
      position: absolute;
      top: 10px;
      right: 10px;
    }
  }

  &-bottom {
    padding: 15px 10px 25px 10px;
    width: 100%;
    background: #27282B;
    border-radius: 0 0 10px 10px;

    .price-old {
      text-decoration-line: line-through;
    }

    .price-new {
      color: $red;
    }
  }
}

